.App {
  /* text-align: center; */
  /* background-color: rgb(19, 19, 19); */
  /* background-image: linear-gradient(rgb(0,0,0), rgb(19, 19, 19)); */
  color: #ffffff;
  min-height: 100vh;
  font-family: 'Montserrat', sans-serif;
  width: 100vw;
}

html {
  overflow-x: hidden;
}

body {
  background-image: linear-gradient(rgb(0,0,0), rgb(19, 19, 19));
}

@media(max-width: 755px) {
  #header-name {
    font-size: 12vw;
    line-height: 12vw;
  }
}

@media(min-width: 756px) {
  #header-name {
    font-size: 96px;
    line-height: 96px;
  }

}

#play-button {
  border-radius: 35px;
  width: 100px;
}

#header {
  width: 100%;
  min-height: 340px;
  max-height: 500px;
  height: 30vh;
  background-image: url(./imgs/SunsetHeader.JPG);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 55%;
  /* background-position: center 5vh; */
  position: relative;
}

#header-gradient {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));
}

#verified {
  padding-left: 17%;
  padding-right: 17%;
  position: absolute;
  bottom: 150px;
}

#header-name {
  /* font-size: 96px;
  line-height: 96px; */
  letter-spacing: -1px;
  font-weight: 800;
  padding-left: 17%;
  padding-right: 17%;
  position: absolute;
  bottom: 50px;
}

#header-btns {
  position: absolute;
  bottom: 10px;
  padding-left: 17%;
  width: 100%;
}

.overviewBtns, .overviewBtns:hover {
  color: white;
  font-weight: 600;
  text-decoration: none;
  transition: 0.15s;
  margin-right: 15px;
  background: transparent;
  border: none;
  padding-bottom: 7px;
}

#portfolioTable {
  max-width: 50%;
  background-color: rgba(0, 0, 0, 0);
  color: white;
}

.overviewBtns:hover {
  color: white;
}

.overviewBtns:focus {
  outline: none;
  color: white;
}

.inactiveBtn {
  color: lightgray;
  border-bottom: 3px solid transparent!important;
}

.activeBtn {
  color: white;
  position: relative;
}

.activeBtn:before {
  content: "";
  position: absolute;
  width: 50%;
  left: 25%;
  bottom: 0;
  height: 1px;
  border-bottom: 3px solid #1db954!important;
}

.containersBody {
  font-family: 'Montserrat', sans-serif!important;
  padding-left: 17%!important;
  padding-right: 17%!important;
  margin: 0!important;
  /* min-height: 38vh; */
}

.section-header {
  font-weight: 600!important;
  font-size: 32px;
  margin-top: 15px;
}

.smallHeader {
  font-size: 20px;
}

#footer {
  background-color: rgb(40, 40, 40);
  width: 100vw;
  /* height: 220px; */
  padding-left: 17%;
  padding-right: 17%;
  padding-top: 2%;
  padding-bottom: 2%;
  font-family: 'Montserrat', sans-serif!important;
  /* text-align: center; */
}

#containerDiv {
  min-height: 100vh;
}

.social-links {
  color: white;
}

.social-links:hover {
  color: gray;
}

@media (prefers-reduced-motion: no-preference) {
  #react-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#react-logo {
  width: 50px;
  height: 50px;
}

.linksReact, .linksReact:hover {
  color: white;
  text-decoration: none;
}